body,
html {
  height: 100%;
  width: 100%;
}

body {
  @include serif-font;
}

hr {
  border-color: $theme-primary;
  border-width: 3px;
  max-width: 50px;
}

hr.light {
  border-color: white;
}

a {
  @include transition-all;
  color: $theme-primary;

  &:focus,
  &:hover {
    color: darken($theme-primary, 10%);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @include sans-serif-font;
}

p {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 20px;
}

.bg-primary {
  background-color: $theme-primary;
}

.bg-dark {
  background-color: $theme-dark;
  color: white;
}

.text-faded {
  color: rgba(white, .7);
}

section {
  padding: 100px 0;
}

aside {
  padding: 50px 0;
}

.no-padding {
  padding: 0;
}
// Home Icon
.home-icon {
  height: 64px;
  padding: 0;
  position: absolute;
  top: 2.5px;
  left: -45px;
}
// Navigation

.navbar {
  min-height: 70px;
}

.navbar-brand{
  position: relative;
}

.navbar-default {
  background-color: white;
  color: $theme-dark;
  border-color: rgba($theme-dark, .5);
  @include sans-serif-font;
  @include transition-all;

  .navbar-header .navbar-brand {
    color: $button-colour;
    @include sans-serif-font;
    font-weight: 700;
    text-transform: uppercase;
    left: 50px;

    &:focus,
    &:hover {
      color: $dark-button;
    }
  }

  .navbar-collapse{
    border-top: none;
  }

  .nav {
    position: relative;
    top: 10px;

    > li {
      > a:focus,
      > a {
        font-weight: 700;
        font-size: 14px;
        color: $theme-dark;

        &:hover {
          color: $theme-primary;
        }
      }

      &.active {
        > a:focus,
        > a {
          color: $theme-primary !important;
          background-color: transparent;

          &:hover {
            background-color: transparent;
          }
        }
      }
    }
  }
  border-color: rgba($theme-dark, .7);
  @media (min-width: 768px) {

    .navbar-header .navbar-brand {
      &:focus,
      &:hover {
        color: darken($button-colour,10%);
      }
    }

    .nav > li > a,
    .nav > li > a:focus {
      &:hover {
        color: lighten($theme-dark, 20%);
      }
    }

    &.affix {
      background-color: white;

      .navbar-header .navbar-brand {
        &:focus,
        &:hover {
          color: darken($button-colour, 10%);
        }
      }

      .nav > li > a,
      .nav > li > a:focus {
        color: $theme-dark;

        &:hover {
          color: $theme-primary;
        }
      }
    }
  }
}

.nav-socials {
margin-top: 17px;
margin-left: auto;
margin-right: auto;
  li a {
    color: $theme-light;
    font-size: 30px;
    &:hover {
      background: inherit;
      color: white;
    }
    &:focus {
      background: inherit;
    }

  }
  @media (min-width: 768px) {
    float: left;
  }
}

// Homepage Header
header {
  position: relative;
  width: 100%;
  min-height: auto;
  @include background-cover;
  @media (min-width: large) {
    background-position: 0px -25px;
}
  background-image: url("/img/2021-05-29-Outdoors.jpeg");
  background-repeat: no-repeat;
  text-align: center;
  color: white;

  .header-content {
    position: relative;
    text-align: center;
    padding: 100px 15px;
    width: 100%;

    .header-content-inner {
      h1 {
        font-weight: 700;
        text-transform: uppercase;
        margin-top: 0;
        margin-bottom: 0;
        text-shadow: 2px 2px 5px black;
      }

      hr {
        margin: 30px auto;
      }

      p {
        font-weight: 400;
        color: white;
        text-shadow: 2px 2px 5px black;
        font-size: 16px;
        margin-bottom: 50px;
      }
    }
  }
  @media (min-width: 768px) {
    min-height: 84%;

    .header-content {
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      padding: 0 50px;

      .header-content-inner {
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;

        p {
          font-size: 18px;
          max-width: 80%;
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
  }
}
// Sections
.section-heading {
  margin-top: 0;
  color: $button-colour;
}

.service-box {
  max-width: 400px;
  margin: 50px auto 0;
  @media (min-width: 992px) {
    margin: 20px auto 0;
  }

  p {
    margin-bottom: 0;
  }
}

.portfolio-box {
  position: relative;
  display: block;
  max-width: 650px;
  margin: 0 auto;

  .portfolio-box-caption {
    color: white;
    opacity: 0;
    display: block;
    background: rgba($theme-primary, .9);
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    height: 100%;
    @include transition-all;

    .portfolio-box-caption-content {
      width: 100%;
      text-align: center;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);

      .project-category,
      .project-name {
        @include sans-serif-font;
        padding: 0 15px;
      }

      .project-category {
        text-transform: uppercase;
        font-weight: 600;
        font-size: 14px;
      }

      .project-name {
        font-size: 18px;
      }
    }
  }

  &:hover {
    .portfolio-box-caption {
      opacity: 1;
    }
  }
  @media (min-width: 768px) {
    .portfolio-box-caption {
      .portfolio-box-caption-content {
        .project-category {
          font-size: 16px;
        }

        .project-name {
          font-size: 22px;
        }
      }
    }
  }
}

.call-to-action {
  h2 {
    margin: 0 auto 20px;
  }
}
// Bootstrap Overrides

.text-primary {
  color: $theme-primary;
}

.no-gutter > [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}
// Button Styles

.btn-default {
  @include button-variant($theme-dark, white, white);
}

.btn-primary {
  @include button-variant(white, $theme-primary, $theme-primary);
}

.btn-secondary{
  @include button-variant(white, $button-colour, $button-colour)
}

.btn {
  @include sans-serif-font;
  border: none;
  border-radius: 300px;
  font-weight: 700;
  text-transform: uppercase;
}

.btn-xl {
  padding: 15px 30px;
}
// Contact

#contact .fa {
  color: $theme-dark;
  font-size: 4em;
}
// Extras
// -- Highlight Color Customization

::-moz-selection {
  color: white;
  text-shadow: none;
  background: $theme-dark;
}

::selection {
  color: white;
  text-shadow: none;
  background: $theme-dark;
}

img::selection {
  color: white;
  background: transparent;
}

img::-moz-selection {
  color: white;
  background: transparent;
}

body {
  -webkit-tap-highlight-color: $theme-dark;
}
